import React, { Component } from 'react'
import Search from '@material-ui/icons/Search';
import { faSearch } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import style from './SearchBar.css'

class SearchBar extends Component {
  render() {
    const {handleClick} = this.props
    const iconSize = "lg"
    return (
      <div className={style.searchForm}>
        <input className={style.searchBar} placeholder="Account Executive"></input>
        <button onClick={handleClick} className={style.searchButton}>
          {/* <Search className={style.searchIcon}></Search> */}
          <FontAwesomeIcon icon={faSearch} size={iconSize}/>
        </button>
      </div>
    )
  }
}
export default SearchBar